.about-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-header-back {
  display: flex;
  align-items: center;
}
.about-header-back-svg {
  @include responsive(width, 20, 24, 420, 520);
  @include responsive(height, 20, 24, 420, 520);
}
.about-header-back-btn {
  background: transparent;
  border: 0;
  outline: 0;
  @include responsive(padding-top, 15, 29, 420, 520);
  @include responsive(padding-bottom, 16, 29, 420, 520);
  @include responsive(padding-left, 18, 28, 420, 520);
  @include responsive(padding-right, 21, 37, 420, 520);
  @include responsive(width, 59, 89, 420, 520);
  @include responsive(height, 51, 81, 420, 520);
}
.about-header-about-text {
  @include responsive(padding-top, 20, 32, 420, 520);
  @include responsive(padding-bottom, 20, 32, 420, 520);
  @include responsive(padding-left, 40, 46, 420, 520);
  @include responsive(padding-right, 40, 46, 420, 520);
  @include responsive(font-size, 16, 25, 420, 520);
  background-color: #df2528;
  color: #fafafa;
  font-family: 'Gilroy';
  font-weight: 800;
  line-height: 0.8;
}
.about-header-call-btn {
  display: none;
}
@media (min-width: 520px) {
  .about-header {
    @include responsive(padding-right, 0, 30, 520, 740);
  }
  .about-header-back-svg {
    @include responsive(width, 24, 24, 520, 740);
    @include responsive(height, 24, 24, 740, 1000);
  }
  .about-header-back-btn {
    @include responsive(padding-top, 29, 29, 520, 740);
    @include responsive(padding-bottom, 29, 29, 520, 740);
    @include responsive(padding-left, 28, 28, 520, 740);
    @include responsive(padding-right, 37, 37, 520, 740);
    @include responsive(width, 89, 89, 520, 740);
    @include responsive(height, 81, 81, 520, 740);
  }
  .about-header-about-text {
    @include responsive(padding-top, 32, 32, 520, 740);
    @include responsive(padding-bottom, 32, 32, 520, 740);
    @include responsive(padding-left, 46, 46, 520, 740);
    @include responsive(padding-right, 46, 46, 520, 740);
    @include responsive(font-size, 25, 25, 520, 740);
    line-height: 0.8;
  }
  .about-header-call-btn {
    display: none;
  }
}
@media (min-width: 740px) {
  .about-header-back-svg {
    @include responsive(width, 24, 24, 740, 1000);
    @include responsive(height, 24, 24, 740, 1000);
  }
  .about-header-back-btn {
    @include responsive(padding-top, 29, 38, 740, 1000);
    @include responsive(padding-bottom, 29, 38, 740, 1000);
    @include responsive(padding-left, 28, 38, 740, 1000);
    @include responsive(padding-right, 37, 38, 740, 1000);
    @include responsive(width, 89, 99, 740, 1000);
    @include responsive(height, 81, 100, 740, 1000);
  }
  .about-header-about-text {
    @include responsive(padding-top, 32, 40, 740, 1000);
    @include responsive(padding-bottom, 32, 40, 740, 1000);
    @include responsive(padding-left, 46, 58, 740, 1000);
    @include responsive(padding-right, 46, 58, 740, 1000);
    @include responsive(font-size, 25, 30, 740, 1000);
    line-height: 0.8;
  }
  .about-header-call-btn {
    display: none;
  }
}
@media (min-width: 1000px) {
  .about-header {
    @include responsive(padding-right, 30, 10, 1000, 1440);
  }
  .about-header-back-svg {
    @include responsive(width, 24, 24, 1000, 1440);
    @include responsive(height, 24, 24, 1000, 1440);
  }
  .about-header-back-btn {  
    @include responsive(padding-top, 38, 38, 1000, 1440);
    @include responsive(padding-bottom, 38, 38, 1000, 1440);
    @include responsive(padding-left, 38, 38, 1000, 1440);
    @include responsive(padding-right, 38, 38, 1000, 1440);
    @include responsive(width, 99, 99, 1000, 1440);
    @include responsive(height, 100, 100, 1000, 1440);
  }
  .about-header-about-text {
    @include responsive(padding-top, 40, 40, 1000, 1440);
    @include responsive(padding-bottom, 40, 40, 1000, 1440);
    @include responsive(padding-left, 58, 58, 1000, 1440);
    @include responsive(padding-right, 58, 58, 1000, 1440);
    @include responsive(font-size, 30, 30, 1000, 1440);
    line-height: 0.8;
  }
  .about-header-call-btn {
    display: block;
    font-family: 'Gilroy';
    font-weight: 800;
    font-size: 18px;
    line-height: 1.2;
    width: 230px;
    height: 58px;
    background: #131926;
    border: 0;
    color: #fafafa;
    border-radius: 29px;
    &:hover {
      background-color: #DF2528;
    }
  }
}
